.mat-toolbar {
  display: flex;
  box-sizing: border-box;
  width: 100%;
  font-size: 20px;
  font-weight: 500;
  font-family: Roboto, "Helvetica Neue", sans-serif;
  padding: 0 16px;
  flex-direction: column; }
  .mat-toolbar .mat-toolbar-row {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    flex-direction: row;
    align-items: center;
    white-space: nowrap; }

.mat-toolbar {
  min-height: 64px; }

.mat-toolbar-row {
  height: 64px; }

@media (max-width: 600px) and (orientation: portrait) {
  .mat-toolbar {
    min-height: 56px; }
  .mat-toolbar-row {
    height: 56px; } }

@media (max-width: 960px) and (orientation: landscape) {
  .mat-toolbar {
    min-height: 48px; }
  .mat-toolbar-row {
    height: 48px; } }
